import { HoverCard, HoverCardContent, HoverCardTrigger } from '@/components/ui/hover-card';
import { FUNCTION_KEYS_ROW, KEYBOARD_LAYOUT, KeyboardProps } from '@/interfaces';
import { Keycap } from './keycap';
import { KEYBOARD_HEIGHT, KEYBOARD_WIDTH } from '@/constants';
import { forwardRef, Ref, RefObject, useImperativeHandle, useRef, useState } from 'react';

type KeyboardLayoutProps = {
  color?: string
  frameColor: string
  fontColor: string
  image?: string;
  type: 'color' | 'image';
  ref: Ref<HTMLDivElement>
}

const FUNCTION_KEYS_ROW_WITH_POSITION = FUNCTION_KEYS_ROW.map((row, idx) => {
  let xOffset = 0;
  return row.map((key, idx) => {
    const keyWidth = key.width || 40;
    const pos = { x: 0, y: 40 };
    xOffset += keyWidth + 4;
    return { key, ...pos };
  })
})

const KEYBORD_LAYOUT_WITH_POSITION = KEYBOARD_LAYOUT.map((row, rowIndex) => {
  let xOffset = 0;
  return row.map((key, idx) => {
    const keyWidth = key.width || 40;
    const pos = { x: xOffset, y: (rowIndex + 1) * 40 };
    xOffset += keyWidth + 4;
    return { key, ...pos };
  })
})

export interface KeyboardLayoutRef {
  setFrameColor: (color: string) => void
  setKeycapColor: (color: string) => void
  setFontColor: (color: string) => void
}

export const KeyboardLayout = forwardRef<KeyboardLayoutRef, KeyboardLayoutProps>(({
  color,
  frameColor,
  fontColor,
  image,
  type,
}, ref) => {
    console.log('hello')
    const frameColorRef = useRef<HTMLDivElement>(null);
    const div2Ref = useRef<HTMLDivElement>(null);

    useImperativeHandle(ref, () => {
      return {
        setFrameColor: (color) => {
          console.log('setting frame color')
          if (frameColorRef.current) {
            frameColorRef.current.style.backgroundColor = color;
          }
        },
        setFontColor: () => {
          console.log('setting font color')
        },
        setKeycapColor: () => {
          console.log('setting keycap color')
        }
      }
    })

    return (
      <div className="flex flex-col items-center justify-center">

        {/* Keyboard Frame Container */}
        <div className="flex flex-col gap-1 w-[666px] p-2 bg-gray-800 border-gray-700 rounded-2xl shadow-xl" ref={frameColorRef}>

          {/* Function Key Row */}
          <div className="w-full justify-between flex gap-3.5">
            {FUNCTION_KEYS_ROW_WITH_POSITION.map((row, index) => (
              <div className="w-full flex justify-between gap-1" key={index}>
                {row.map((keyWithPos, innerIndex) => {
                  return (
                    <RenderKey
                      type={type}
                      keyProps={keyWithPos.key}
                      fontColor={fontColor}
                      color={color}
                      image={image}
                      key={innerIndex}
                      xPos={keyWithPos.x}
                      yPos={keyWithPos.y}
                    />
                  );
                })}
              </div>
            ))}
          </div>

          {/* Main Keyboard Rows */}
          {KEYBORD_LAYOUT_WITH_POSITION.map((row, innerIndex) => {
            return (
              <div key={innerIndex} className="w-full justify-between flex gap-1">
                {row.map((keyWithPos, innerIndex) => {
                  return (
                    <RenderKey
                      type={type}
                      keyProps={keyWithPos.key}
                      fontColor={fontColor}
                      color={color}
                      image={image}
                      key={innerIndex}
                      xPos={keyWithPos.x}
                      yPos={keyWithPos.y}
                    />
                  );
                })}
              </div>
            );
          })}
        </div>
      </div>

    );
  });

const RenderKey = ({
  keyProps,
  image,
  color,
  fontColor,
  type,
  xPos,
  yPos,
}: {
    keyProps: KeyboardProps;
    type: 'color' | 'image';
    color: string | undefined;
    fontColor: string;
    image: string | undefined
    xPos: number;
    yPos: number;
  }) => {
  return (
    <HoverCard>
      <HoverCardTrigger>
        <Keycap
          type={type}
          backgroundColor={color}
          fontColor={fontColor}
          shiftKey={keyProps.shiftKey}
          keyChar={keyProps.key}
          keyWidth={keyProps.width}
          imageUrl={image || ''}
          imageStyle={
            type === 'image' && image
              ? {
                backgroundImage: `url(${image})`,
                backgroundSize: `${KEYBOARD_WIDTH}px ${KEYBOARD_HEIGHT}px`,
                backgroundPosition: `-${xPos}px -${yPos}px`,
                backgroundRepeat: 'no-repeat',
                backgroundClip: 'border-box',
              }
              : undefined
          }
        />
      </HoverCardTrigger>
      <HoverCardContent>
        The React Framework – created and maintained by @vercel.
        <input type="color" id={`${keyProps}-color`} />
      </HoverCardContent>
    </HoverCard>
  )
}

